  header.navbar .navbar-header .navbar-toggle {
    -webkit-box-shadow: 1px -1px 0 #333;
    box-shadow: 1px -1px 0 #333;
  }
}
header.navbar .navbar-header .navbar-toggle:hover {
  background: #00FFFF;
}
