}
.showNav header.navbar .navbar-header .navbar-toggle {
  background: #FFF;
}
.showNav header.navbar .navbar-header .navbar-toggle .icon-bar:before,
.showNav header.navbar .navbar-header .navbar-toggle .icon-bar:after {
  background: #000;
}
.tab-pressed body header.navbar .navbar-header .navbar-toggle:focus {
